<template>
    <div class="modal-layout">
    <div class="darken" @click="closeModal">
    <div class="new-modal fill-wallet-modal" @click.stop="">
            <div class="new-modal-back-button-container">
                <button class="new-modal-back-button" @click="closeModal"></button>
            </div>
        <div class = "modal-content">
        <div class = "title-wrapper">
            <div class = "title">
                {{`${$localizeFilter('fill_up_a_wallet')} ${coin.ticker}`}}
            </div>
            <img class="token" :src="require(`@/assets/newImages/coins/${coin.name}.png`)">
        </div>
        <div class="buttons-block">
            <button class="fill-type-button" v-for="(fillTypeCoin,idx) in fillTypeCoins" v-bind:key="idx" @click="selectFillType(fillTypeCoin)">
                <img class="type-coin-image" :src="require(`@/assets/newImages/coins/${fillTypeCoin.name}.png`)">
                <div class="fill-type-text">
                    {{`${$localizeFilter('purchase_for')} ${fillTypeCoin.ticker}`}}
                </div>
            </button>
        </div>
    </div>
    </div>
    </div>
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'

export default defineComponent({
  props: {
      coin: {
          type: Object,
          required: true
      },
  },
  setup() {
    
  },
  data: ()=>({
        fillTypeCoins: [{name: 'GadgetCoin', ticker: 'GDC'},
        {name: 'USDT', ticker: 'USDT'},
        {name: 'Ethereum', ticker: 'ETH'}]
  }),
  methods: {
      closeModal(reload) {
          this.$emit('closeModal', reload)
      },
      selectFillType(fillTypeCoin) {
          const buyCryptoData = {
              buyCoin: this.coin,
              sellCoin: fillTypeCoin
          }
          this.$emit('selectFillType', buyCryptoData)
      }
  }
})
</script>


