<script>
import { defineComponent } from '@vue/composition-api'
import useVuelidate from "@vuelidate/core";
import { helpers, required, numeric, minValue, minLength, maxLength } from "@vuelidate/validators";
import localizeFilter from '@/filters/localize.filter'

export default defineComponent({
    props: {
        coin: {
            type: Object,
            required: true
        }
    },
    validations() {
        return {
            amount: {
                // required: helpers.withMessage(({ 
                //     $pending,
                //     $invalid,
                //     $params,
                //     $model
                // })=>`${$pending} ${$invalid} ${$params} ${$model}`, required),
                required: helpers.withMessage(localizeFilter('field_must_not_be_empty'), required),
                numeric,
                minValue: minValue(this.minAmount),
            },
            walletAddress: {
                required: helpers.withMessage(localizeFilter('field_must_not_be_empty'), required),
                minLength: minLength(26),
                maxLength: maxLength(35)
            }
        };
    },
    setup() {
        return { v$: useVuelidate() };
    },
    data: ()=>({
            amount: '',
            minAmount: 0,
            walletAddress: '',
            walletType: 'Bitcoin'
    }),
    methods: {
        closeModal(reload) {
            this.$emit('closeModal', reload)
        },
        async submitHandler() {
            this.v$.$touch();
            if (this.v$.$error /*|| !this.conditionsAccepted*/) {
                return;
            }
        }
    }
})
</script>


