<template>
    <div class = "balance-body">
        <div class="first-row">
            <div class="balance-title">{{`${$localizeFilter('balance')}:`}}</div>
            <span class = "balance-rate-change"><span :class="(rateChange >= 0 ? 'green-text' : 'red-text')">
                {{`${(rateChange >= 0 ? '+':'-')} ${$utils.formatNumber(Math.abs(rateChange*100), 'en-US', 0, 2)}% `}}
            </span>{{`/ 24h`}}</span>
        </div>
        <div class = "balance-amount">{{`${coinTicker} ${$utils.formatNumber(amount, 'en-US', 8, 8)}`}}</div>
        <div class = "balance-usd">{{`≈ ${$utils.formatCurrency(amountUSD, 'USD', 'en-US', 0, 2)}`}}</div>
    </div>
</template>

<script>
export default {
    props: {
        coinTicker: {
            type: String,
            required: true
        },
        data: {
            type: Object,
            default: null
        }
    },
  data: () => ({ }),
  computed: {
    amount() {
        return this.data?.amount || 0
    },
    amountUSD() {
        return this.data?.amountUSD || 0
    },
    rateChange() {
        return this.data?.rateChange || 0
    }
  },
  methods: { },
}
</script>

