<template>
    <div class="coins-pairs-list-container slider" :id="containerId">
        <div class="coins-pairs-list slides" :id="listId" @scroll="handleScroll">
            <div v-for="(page, i) in pagesCount" :key="i" class="coins-pairs-page slide" :id="getPageId(i)">
                <div v-for="(item, j) in itemsPerPage" :key="j" class="coins-pair-block" @click="openPair(coinsPairs[(i*itemsPerPage)+j])">
                <CoinsPair 
                v-if="coinsPairs[(i*itemsPerPage)+j]"
                :style="`width: ${itemWidth}px!important`"
                :firstCoin="coinsPairs[(i*itemsPerPage)+j].firstCoin"
                :lastCoin="coinsPairs[(i*itemsPerPage)+j].lastCoin"
                :rate="coinsPairs[(i*itemsPerPage)+j].rate"
                :rateChange="coinsPairs[(i*itemsPerPage)+j].rateChange"/>
                 <div v-else></div>
                </div>
            </div>
        </div>
        <div class="page-indicator-container" v-if="coinsPairs.length > itemsPerPage">
            <div v-for="(page,i) in pagesCount" :key="i" class="page-indicator-block" @click="turnToThePage(i)">
                <div class="page-indicator" :class="{'active':(currentPage===page)}"></div>
            </div>
        </div>
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import CoinsPair from '@/components/CoinsPair'
export default defineComponent({
    props: {
        coinTicker: {
            type: String,
            required: true
        }
    },
    data: ()=>({
        currentPage: 1,
        mountDate: +Date.now(),
        itemsPerPage: 4,
        itemWidth: 184
    }),
    components: {
        CoinsPair
    },
    computed: {
        coinsPairs() {
            return this.$store.getters.exchangeMajorCoinsPairs(this.coinTicker)?.pairs ?? []
        },
        pagesCount() {
            return Math.ceil((this.coinsPairs.length) / this.itemsPerPage)
        },
        listId() {
            return `coinsPairsList-${this.mountDate}`
        },
        containerId() {
            return `coinsPairsListContainer-${this.mountDate}`
        },
        itemsPerCurrentPage() {
             if (this.currentPage === this.pagesCount) {
                if (this.coinsPairs.length % this.itemsPerPage === 0) {
                    return this.itemsPerPage
                } else {
                    return this.coinsPairs.length % this.itemsPerPage
                }
            } else {
                return this.itemsPerPage
            }
        }
    },
    methods: {
        openPair(pair) {
            this.$router.push(`exchange?firstCoin=${pair.firstCoin}&lastCoin=${pair.lastCoin}`)
        },
        getPageId(i) {
            return `coinsPairsPage${i}` //-${this.mountDate}`
        },
        handleScroll() {
            const el = document.getElementById(this.listId)
            if (el) {
                 const pageWidth = el.offsetWidth
                 this.currentPage = Math.floor(el.scrollLeft / pageWidth) + 1
            }
        },
        turnToThePage(i) {
            const el = document.getElementById(this.listId)
            const pageWidth = el.offsetWidth
            if (el) {
                el.scrollLeft = pageWidth * i
            }
        }
    },
    setup() {
        
    },
    mounted() {
        const el = document.getElementById(this.listId)
        if (el) {
            const pageWidth = el.offsetWidth
            const gap = 10
            if (window.innerWidth >= 1200) {
                this.itemWidth = Math.floor((pageWidth  - 3*gap) / this.itemsPerPage)
            } else {
                this.itemWidth = Math.floor((pageWidth  - 1*gap) / (this.itemsPerPage / 2))
            }
            // console.log(this.itemWidth)
            // el.addEventListener('scroll', this.handleScroll)
        }
    },
    unmounted() {
        // const el = document.getElementById(this.listId)
        // if (el) {
        //     el.removeEventListener('scroll', this.handleScroll)
        // }
    },
    created() {
        
    }
})
</script>

